import { FormArray, FormBuilder, FormGroup, Validators, FormGroupDirective } from '@angular/forms';
import { ApiService } from './../../../../shared/services/api.service';
import { ChangeDetectorRef, Component, ElementRef, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CorporateOnboardingService } from '../corporate-onboarding.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { MatAutocomplete, MatAutocompleteTrigger } from '@angular/material/autocomplete';
