  fieldArray: Array<any> = [];
  private newAttribute: any = {};
  arrData = [];
  directorCount = 1;
  showHide: boolean;
  showcard: any[];
  deletecount: number;
  showonlydirectorDetails: boolean;
  NameOfDirector: string
