  corporateId: any;
  customerId:any;
  arrayState: string[] = [] ;
  arrayCountry: string[] = [];
  arrayCity: string[] = [];
  arrayResidence: string[];
  profileImage: any = "not_available";
  initials: any;
  addressTypeList: string[];
  noOfDir: any;
  saveStatus: any[] = [];
  showAutocomplete: boolean;
  customerInfoList: any[] = [];
  existingCustomer: boolean;
  isEdit: boolean = false;
  isDirectorSaved:boolean=false;
  Currency: any;
  currencyCode: any;
  country: void;
  // zipcode: any;
  cifNumber: any;
